import * as React from 'react';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Box from "@mui/material/Box";

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
    },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
        border: 0,
    },
}));

function createOBData(name_ja, name_en, grad_year, type, title) {
    return { name_ja, name_en, grad_year, type, title };
}

const rows = [
    createOBData(" ", "Asano Yusuke", "2024", "", ""),
    createOBData(" ", "Uezono Yuma", "2024", "", ""),
    createOBData("", "en name", "2024", "", ""),
    createOBData("","en name" , "2024", "", ""),
    createOBData(" ", "Tanaka Yu", "2024",  "", ""),
    createOBData(" ", "Hashimoto Tatsuhiro", "2024", "", ""),
    createOBData(" ","en name", "2023", "",""),
    createOBData("", "en name", "2023", "", "Python"),
    createOBData(" ", "Nishikawa Takuma", "2023", "", ""),
    createOBData(" ", "Yamaguchi Keita", "2023", "", ""),
    createOBData(" ", "Yoshimoto Sora", "2023", "", "AI"),
    createOBData("", "en name", "2023", "", "C"),
    createOBData("", "en name", "2023", "", "e"),
    createOBData(" ","en name", "2022", "", "EC"),
    createOBData(" ", "en name", "2022", "", ""),
    createOBData(" ", "en name", "2022", "", "3DESHMAC"),
    createOBData(" ", "en name", "2021", "", ""),
    createOBData("", "en name", "2021", "", ""),
    createOBData("", "en name", "2021", "", "")
];

export default function Member() {
    return (
        <Box py={8}>
            <h2> 2022/07/20 </h2>

            <TableContainer component={Paper}>
                <Table sx={{ minWidth: 700 }} aria-label="customized table">
                    <TableHead>
                        <TableRow>
                            <StyledTableCell>Name</StyledTableCell>
                            <StyledTableCell align="right"></StyledTableCell>
                            <StyledTableCell align="right"></StyledTableCell>
                            <StyledTableCell align="right"></StyledTableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <StyledTableRow key={row.name_ja}>
                                <StyledTableCell component="th" scope="row">
                                    {row.name_ja} ({row.name_en})
                                </StyledTableCell>
                                <StyledTableCell align="right">{row.grad_year}</StyledTableCell>
                                <StyledTableCell align="right">{row.type}</StyledTableCell>
                                <StyledTableCell align="right">{row.title}</StyledTableCell>
                            </StyledTableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </Box>
    );
}